import React from 'react'

export default function CommentsList (props) {

    return (

        <React.Fragment>
        <h3>Comments:</h3>

        {props.comments.map((comment,i) => (
            <div key={i} className="comment">
                <h4>{comment.username}</h4>
                <p>{comment.text}</p>
            </div>
        ))}
        </React.Fragment>
    )
}