import React from 'react'
import Button from '@material-ui/core/Button'

export default function UpvotesSection(props) {

    const upvoteArticle=async () => {
        const result = await fetch(`/api/articles/${props.articleName}/upvote`, {
            method: 'post'
        })

        const body = await result.json();
        props.setArticleInfo(body);
    }

    return (

        <div id="upvotes-section">
            <Button variant="contained" color="primary" onClick={() => upvoteArticle()}>
                Add Upvote
            </Button>
            <p>
                This post has been upvoted {props.upvotes} times.
            </p>
        </div>
        
    )
}