import React, { useState, useEffect } from 'react'
import articleContent from './Article-Content'
import NotFoundPage from '../pages/NotFoundPage'
import ArticleList from '../Components/ArticleList'
import CommentsList from './CommentsList'
import UpvotesSection from './UpvotesSection'
import AddCommentsForm from './AddCommentsForm'

function ArticlePage ({match}) {

    const name = match.params.name;
    const article = articleContent.find(article => article.name === name);

    const [articleInfo, setArticleInfo] = useState({username: 0, comments: []});
    
    useEffect(() => {
        const fetchData = async () => {
            const result = await fetch(`/api/articles/${name}`)
            const body = await result.json();
            setArticleInfo(body);
        }
        fetchData();
    },[name])

    const otherArticleList = articleContent.filter(article => article.name !== name);

    if (!article) return ( <h1> <NotFoundPage /></h1>)

    return(
        <React.Fragment>
            <h1>{article.title}</h1>
            <UpvotesSection articleName={name} upvotes={articleInfo.upvotes} setArticleInfo={setArticleInfo} />
            {article.content.map((item,i) => (
                <p key={i}>{item}</p>
            ))}

            <CommentsList comments={articleInfo.comments} />

            <AddCommentsForm articleName={name} setArticleInfo={setArticleInfo} />
        
            <h3>
                Other Articles:
            </h3>
        <ArticleList articles={otherArticleList} />
        </React.Fragment>
    )
}

