import React, { useState } from 'react'
import TextField from '@material-ui/core/TextField'
import Button from '@material-ui/core/Button'

export default function UpvotesSection(props) {

    const [username, setUsername]=useState('')
    const [commentsText, setCommentsText]=useState('')

    const addComment=async () => {
        const result = await fetch(`/api/articles/${props.articleName}/add-comment`, {
            method: 'post',
            body: JSON.stringify({username, text: commentsText},null,2),
            headers: {
                'Content-Type': 'application/json',
            }
        })

        const body = await result.json();
        props.setArticleInfo(body);
        setUsername('')
        setCommentsText('')
    }

    return (
        <div id="add-comment-form">
            <h3> Add a Comment</h3>
            <TextField id="standard-basic" label="Name" value={username} onChange={(event) => setUsername(event.target.value)} />
            <TextField id="outlined-multiline-static" label="Comment Text" multiline rows={4} variant="outlined" value={commentsText} onChange={(event) => setCommentsText(event.target.value)}/>
            <Button variant="contained" color="primary" onClick={() => addComment()}>
                Add Comment
            </Button>
        </div>
    )
}