import React from 'react'
import { Link } from 'react-router-dom'

export default function ArticleList(props) {

    return (
        <React.Fragment>
            {props.articles.map((article,i) => (
                <Link key={i} to={`/article/${article.name}`}>
                    <h3>
                        {article.title}
                    </h3>
                    <p>
                        {article.content[0].substring(0,150)}...
                    </p>
                    <hr />
                </Link>
            ))}
        </React.Fragment>
    )
}